.contacts {
  display: flex;
  padding: 49px 0 45px;
  background-color: $color-default-white;

  @include vp-1199 {
    padding: 62px 133px 46px;
    justify-content: center;
  }

  @include vp-767 {
    padding: 64px 0 46px;
  }
}

.contacts__container {
  padding-left: 103px;
  margin-right: auto;

  @media (max-width: 1365px) {
    padding: 0 103px;
    min-width: 500px;
  }

  @include vp-1199 {
    padding: 0;
    margin: 0;
    min-width: 320px;
  }
}

.contacts__wrapper {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: min-content min-content;
  column-gap: 65px;
  margin-left: 17px;

  @include vp-1199 {
    min-width: 502px;
    width: 100%;
    grid-template-columns: 55% 40%;
    column-gap: 37px;
    padding: 0;
    margin-left: 0;
  }

  @include vp-767 {
    min-width: 320px;
    display: flex;
    flex-direction: column;
    column-gap: 0;
    margin: 0;
    padding: 0 15px;
  }
}

.contacts h2 {
  font-family: $ff-alumni;
  font-weight: $fw-bold;
  font-size: $fs-large;
  line-height: $lh-huge;
  letter-spacing: $ls-medium;
  margin: 0;
  margin-bottom: 26px;
  padding: 0;
  color: $color-sapphire;
  grid-row: 1/2;
  grid-column: 1/3;

  @include vp-1199 {
    font-size: $fz-xxbig;
    line-height: $lh-slarge;
    margin-bottom: 30px;
  }

  @include vp-767 {
    font-weight: $fw-semi-bold;
    font-size: $fz-big;
    line-height: $fz-sbig;
    margin-bottom: 32px;
  }
}

.contacts__list {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.contacts__list--modif {
  position: relative;

  &::after {
    position: absolute;
    top: 6px;
    right: -10px;
    content: "";
    width: 1px;
    height: 78%;
    background-color: $color-light-slate-grey;
  }

  @include vp-1199 {
    padding: 0;

    &::after {
      top: 10px;
      right: 10px;
      width: 1px;
      height: 80%;
    }
  }

  @include vp-767 {
    &::after {
      content: none;
    }
  }
}

.contacts__list dt {
  font-family: $ff-roboto;
  font-weight: $fw-bold;
  font-size: $fz-xxsmall;
  line-height: $lh-xxxmedium;
  color: $color-sapphire;
  margin: 0;
  padding: 0;
  padding-left: 3px;

  @include vp-1199 {
    font-size: $fz-medium;
    line-height: $lh-xsbig;
    padding-left: 0;
    margin-bottom: 3px;
  }

  @include vp-767 {
    padding-left: 0;
    margin-bottom: 0;
  }
}

.contacts__list dd {
  font-family: $ff-roboto;
  font-weight: $fw-normal;
  font-size: $fz-xsmall;
  line-height: $lh-xlmedium;
  color: $color-sapphire;
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  padding-left: 3px;

  @include vp-1199 {
    font-size: $fz-xxxsmall;
    line-height: $lh-xxlxmedium;
    padding-left: 0;
    margin-bottom: 18px;
  }

  @include vp-767 {
    margin-bottom: 21px;
    transform: translateY(4px);
  }
}

.contacts__list a {
  font-family: $ff-roboto;
  font-weight: $fw-normal;
  font-size: $fz-xsmall;
  line-height: $lh-xlmedium;
  color: $color-sapphire;
  text-decoration: none;
  cursor: pointer;
  margin: 0;
  padding: 0;

  @include vp-1199 {
    font-size: $fz-xxxsmall;
    line-height: $lh-xxlxmedium;
  }
}
