.events {
  padding: 118px 0 106px;
  display: flex;
  justify-content: center;

  @include vp-1199 {
    padding: 99px 0 101px;
  }

  @include vp-767 {
    padding: 70px 15px 64px;
  }
}

.events__container {
  display: flex;
  max-width: 1600px;

  @include vp-1199 {
    flex-direction: row;
    flex: 1 1 100%;
  }

  @include vp-767 {
    flex-direction: column;
  }
}

.events__wrapper {
  display: flex;
  justify-content: center;
  background-color: $color-torch-red;


  @include vp-1199 {
    width: 43%;
  }

  @include vp-767 {
    background-color: $color-torch-red;
    padding: 0;
    width: 100%;
  }
}

.events__content {
  position: relative;
  display: grid;
  grid-template-columns: min-content auto min-content;
  grid-template-rows: repeat(4, min-content);
  padding: 81px 78px 80px 79px;
  margin: 0;
  flex-wrap: wrap;
  width: 500px;
  background-color: $color-torch-red;

  @include vp-1199 {
    padding: 46px 44px;
  }

  @include vp-767 {
    min-width: 100%;
    padding: 46px 44px 50px;
  }
}

.events__content h2 {
  font-family: $ff-alumni;
  font-weight: $fw-semi-bold;
  font-size: $fz-huge;
  line-height: $lh-shuge;
  letter-spacing: $ls-medium;
  color: $color-default-white;
  padding: 5px 0 0 11px;
  margin: 0;
  grid-column: 1/2;
  grid-row: 1/3;
  text-align: center;
  text-transform: uppercase;

  @include vp-1199 {
    font-size: $fz-xxxlbig;
    line-height: $lh-xxxbig;
    padding: 5px 0 0 7px;
  }

  @include vp-767 {
    padding: 4px 0 0 6px;
  }
}

.events__content p {
  padding: 9px 0 0 0;
  margin: 0;
  grid-row: 3/4;
  grid-column: 1/4;
  font-family: $ff-roboto;
  font-weight: $fw-normal;
  font-size: $fz-xxxsmall;
  line-height: $lh-xxlxmedium;
  color: $color-default-white;

  @include vp-1199 {
    font-size: $fz-small;
    line-height: $lh-xxxsmall;
    padding: 0;
  }
}

.events__content dl {
  grid-column: 3/3;
  margin: 0;
  display: grid;
  row-gap: 8px;

  @include vp-1199 {
    row-gap: 0;
  }
}

.events__content dt {
  color: $color-default-white;
  padding: 0;
  margin: 0;
  font-family: $ff-roboto;
  font-weight: $fw-normal;
  font-size: $fz-xxxsmall;
  line-height: $lh-xxlxmedium;

  @include vp-1199 {
    font-size: $fz-small;
    line-height: $lh-xxxsmall;
    transform: translateX(-2px);
  }
}

.events__content dd {
  position: relative;
  color: $color-default-white;
  padding: 0;
  margin: 0;
  font-family: $ff-roboto;
  font-weight: $fw-bold;
  font-size: $fz-xbig;
  line-height: $lh-xllarge;
  display: flex;
  width: min-content;

  &::after {
    content: "";
    position: absolute;
    height: 1px;
    background-color: $color-default-white;
    top: 4px;
    left: 0;
    width: 100%;
  }

  @include vp-1199 {
    font-size: $fz-xxmedium;
    line-height: 35px;
    transform: translateX(-2px);

    &::after {
      top: 2px;
    }
  }
}

.events__content span {
  font-weight: $fw-normal;
  font-size: $fz-xsmall;
  line-height: $lh-xlmedium;
  display: block;
  padding-top: 9px;
  padding-left: 7px;
  margin: 0;

  @include vp-1199 {
    font-size: $fz-ssmall;
    line-height: $lh-small;
    padding-top: 5px;
    padding-left: 4px;
  }
}

.events__content a {
  margin-top: 46px;
  padding: 24px 40px;
  text-align: center;
  min-width: 343px;
  grid-column: 1/4;
  grid-row: 4/4;
  font-family: $ff-roboto;
  font-weight: $fw-bold;
  font-size: $fz-small;
  line-height: $lh-xsmall;
  letter-spacing: $ls-interium;
  color: $color-default-white;
  text-transform: uppercase;
  transition: color $trans-default;
  border: 4px solid $color-default-white;
  outline: none;
  user-select: none;

  @include hover-focus {
    background-color: $color-default-white;
    border-color: $color-default-white;
    color: $color-egyptian-blue;

    &:active {
      color: $color-sapphire;
    }
  }

  @include vp-1199 {
    font-size: $fz-ssmall;
    line-height: $lh-small;
    padding: 12px 20px;
    min-width: 204px;
    border: 2px solid $color-default-white;
    margin-top: 31px;
  }

  @include vp-767 {
    margin-top: 32px;
    min-width: 202px;
  }
}

.events__poster {
  position: relative;
  overflow: hidden;
  min-height: 500px;
  flex: 1 1 100%;

  @include vp-1199 {
    min-height: 292px;
    flex: 1 1 70%;
  }

  @include vp-767 {
    min-width: 290px;
    min-height: 292px;
  }
}

.events__poster img {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: $color-dark-cerulean;
  object-fit: cover;
  object-position: center;
  top: 0;
  left: 0;
}
